body{
    margin: 0;
    background-color: rgb(222, 209, 185)  ;
    font-family: sans-serif;
}

input[type=checkbox] {
    accent-color: rgb(78, 75, 75);
    margin-right: 10px;
  }

.todo-container{
    width: 350px;
 margin: auto;
 margin-top: 30px;
  
    text-align: center;
}
.input-section{
    background-color: #ececec;
    padding: 18px;
    border-radius: 10px;
    box-shadow: 0 4px 6px #15141466;


}

.input-section input{
    border-radius: 5px;
    border: 1px solid silver;
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    outline: none;  

}

ul{
    padding: 0;
    text-align: left;
    color: grey;
}

ul li i{
    text-align: right;
    flex: 1 1;
    cursor: pointer;

}

ul li{
    display: flex;
    background-color: #fff;
    list-style: none;
    border-radius: 5px;
    padding: 15px;
    margin-bottom: 10px ;
    box-shadow: 0 4px 6px #00000066;
}

h1{
    margin: 0;
    margin-bottom: 15px;
    color: #ababab;
}

input::-webkit-input-placeholder{
    color: silver;
}

input::placeholder{
    color: silver;
}
